export const __metadata = { PACKAGE_NAME: '@wix/bookings' };

export function createResourceType(
  httpClient: HttpClient
): CreateResourceTypeSignature {
  return (resourceType: NonNullablePaths<ResourceType, `name`, 2>) =>
    universalCreateResourceType(
      resourceType,
      // @ts-ignore
      { httpClient }
    );
}

interface CreateResourceTypeSignature {
  /**
   * Creates a new resource type.
   * @param - Resource type to create.
   * @returns Created resource type.
   */
  (resourceType: NonNullablePaths<ResourceType, `name`, 2>): Promise<
    ResourceType & {
      __applicationErrorsType?: CreateResourceTypeApplicationErrors;
    }
  >;
}

export function getResourceType(
  httpClient: HttpClient
): GetResourceTypeSignature {
  return (resourceTypeId: string) =>
    universalGetResourceType(
      resourceTypeId,
      // @ts-ignore
      { httpClient }
    );
}

interface GetResourceTypeSignature {
  /**
   * Retrieves a resource type.
   * @param - ID of the resource type to retrieve.
   * @returns Retrieved resource type.
   */
  (resourceTypeId: string): Promise<ResourceType>;
}

export function updateResourceType(
  httpClient: HttpClient
): UpdateResourceTypeSignature {
  return (
    _id: string,
    resourceType: NonNullablePaths<UpdateResourceType, `revision`, 2>
  ) =>
    universalUpdateResourceType(
      _id,
      resourceType,
      // @ts-ignore
      { httpClient }
    );
}

interface UpdateResourceTypeSignature {
  /**
   * Updates a resource type.
   *
   *
   * Each time the resource type is updated, `revision` increments by 1.
   * You must include current revision of the resource type when updating it.
   * This ensures you're working with the latest service information and prevents unintended overwrites.
   * @param - Resource type ID.
   * @returns Updated resource type.
   */
  (
    _id: string,
    resourceType: NonNullablePaths<UpdateResourceType, `revision`, 2>
  ): Promise<
    ResourceType & {
      __applicationErrorsType?: UpdateResourceTypeApplicationErrors;
    }
  >;
}

